/* COLOR DEFINITIONS */
$charcoal: #111111;
$razzmatazz: #f2226b;
$jonquil: #f9cc67;

/* GREYSCALE */
$grey-mid: #cccccc;
$white: #ffffff;
$black: #000000;

/* COLOR SWATCHES */
$primary: $razzmatazz;
$secondary: $jonquil;

/* COLOR ASSIGNMENTS */
$color-background-primary: $charcoal;
$color-text: $white;
$color-link: $primary;
$color-hover: $secondary;
$color-placeholder: $grey-mid;
$color-border: $razzmatazz;
$color-disabled: #8E8E95;